
.related {
  padding: 0 0.06019rem;
}
.related .related-content div {
    display: inline-block;
    font-size: 12.4px;
    line-height: 1.1;
    font-weight: 500;
}
.related .related-content .related-tag {
    width: 0.09259rem;
    max-height: 0.0463rem;
    color: rgba(0, 0, 0, 0.2);
}
.related .related-content .related-title {
    max-width: 0.64815rem;
}
.related .related-author {
    margin-left: 0.1rem;
    margin-bottom: 0.04444rem;
    font-size: 12px;
    -webkit-transform-origin-x: 0;
    -webkit-transform: scale(0.84);
    color: rgba(0, 0, 0, 0.35);
}



