
#music-player {
  position: fixed;
  height: 100%;
  width: 100%;
  overflow: hidden;
}
#music-player header {
    position: relative;
    padding: 0 0.03889rem;
    background-color: white;
}
#music-player header .music-title {
      padding-top: 0.02315rem;
      height: 0.11574rem;
      color: #a2a2a2;
      text-align: center;
      letter-spacing: 1px;
      line-height: 0.09259rem;
      font-size: 12.9px;
      text-shadow: 0.00093rem 0.00093rem 0.00093rem #a2a2a2;
}
#music-player header input[type=range] {
      -webkit-appearance: none;
      width: 100%;
      height: 21px;
      background: -webkit-linear-gradient(rgba(0, 0, 0, 0.35), rgba(0, 0, 0, 0.35)) no-repeat, -webkit-linear-gradient(black, black) no-repeat;
      background: linear-gradient(rgba(0, 0, 0, 0.35), rgba(0, 0, 0, 0.35)) no-repeat, linear-gradient(black, black) no-repeat;
      background-size: 100% 3px, 0% 3px;
      background-position: 0 9px, 0 9px;
}
#music-player header input[type=range]:focus {
      outline: none;
}
#music-player header input[type=range]::-webkit-slider-thumb {
      -webkit-appearance: none;
      margin-top: 6px;
      height: 10px;
      width: 10px;
      border-radius: 50%;
      border: 3px solid black;
      box-shadow: 0px 1px 3px black;
      background-color: red;
}
#music-player header input[type=range]::-webkit-slider-runnable-track {
      height: 21px;
}
#music-player header .timer {
      position: absolute;
      top: 0.15278rem;
      right: 0.02222rem;
      color: #c8c8c8;
      -webkit-transform-origin-x: 0;
      -webkit-transform: scale(0.8);
      font-size: 12px;
}
#music-player header .singer {
      height: 0.06481rem;
      padding-top: 0.00926rem;
      text-align: center;
      -webkit-box-align: center;
                  -ms-flex-align: center;
              align-items: center;
      line-height: 0.05556rem;
      color: #c8c8c8;
      font-size: 11px;
}
#music-player header .play-box {
      display: -webkit-box;
      display: -ms-flexbox;
      display: -webkit-flex;
      display: flex;
      height: 0.10648rem;
      -webkit-box-pack: center;
                  -ms-flex-pack: center;
              justify-content: center;
      -webkit-box-align: center;
                  -ms-flex-align: center;
              align-items: center;
}
#music-player header .play-box .last, #music-player header .play-box .next {
        height: 0.09259rem;
}
#music-player header .play-box .last, #music-player header .play-box .next {
        position: relative;
        height: 0.04074rem;
        width: 0.07963rem;
}
#music-player header .play-box .last:before, #music-player header .play-box .last:after, #music-player header .play-box .next:before, #music-player header .play-box .next:after {
        content: '';
        display: block;
        position: absolute;
        top: -0.00278rem;
        border-left: 0.03981rem solid transparent;
        border-top: 0.02315rem solid transparent;
        border-bottom: 0.02315rem solid transparent;
        border-right: 0.03981rem solid transparent;
}
#music-player header .play-box .last {
        border-left: 0.00926rem solid black;
}
#music-player header .play-box .last:before {
          left: -0.04537rem;
          border-right: 0.03981rem solid black;
}
#music-player header .play-box .last:after {
          left: -0.01204rem;
          border-right: 0.03981rem solid black;
}
#music-player header .play-box .last:active {
        border-left-color: #646464;
}
#music-player header .play-box .last:active:before, #music-player header .play-box .last:active:after {
          border-right-color: #646464;
}
#music-player header .play-box .next {
        border-right: 0.00926rem solid black;
}
#music-player header .play-box .next:before {
          right: -0.04537rem;
          border-left: 0.03981rem solid black;
}
#music-player header .play-box .next:after {
          right: -0.01204rem;
          border-left: 0.03981rem solid black;
}
#music-player header .play-box .next:active {
        border-right-color: #646464;
}
#music-player header .play-box .next:active:before, #music-player header .play-box .next:active:after {
          border-left-color: #646464;
}
#music-player header .play-box .play {
        height: 0.05556rem;
        width: 0.04444rem;
        margin: 0 0.07407rem;
        background: -webkit-linear-gradient(left, black 33.33%, white 0, white 66.66%, black 0, black 100%);
        background: linear-gradient(to right, black 33.33%, white 0, white 66.66%, black 0, black 100%);
}
#music-player header .play-box .pause {
        height: 0.05185rem;
        width: 0.08889rem;
        margin-left: 0.07407rem;
        margin-right: 0.02963rem;
        border-left: 0.04444rem solid black;
        border-right: 0.04444rem solid transparent;
        border-top: 0.02593rem solid transparent;
        border-bottom: 0.02593rem solid transparent;
}
#music-player header .btn-box {
      position: relative;
      height: 0.12963rem;
      padding-top: 0.00926rem;
      text-align: center;
      line-height: 0.12963rem;
}
#music-player header .btn-box .cycle-mode, #music-player header .btn-box .collect, #music-player header .btn-box .detail-content {
        position: absolute;
        top: 0.04167rem;
        height: 0.06019rem;
        width: 0.07407rem;
        background-size: contain;
        background-repeat: no-repeat;
}
#music-player header .btn-box .cycle-mode {
        left: 0;
        background-image: url("../assets/player_single_cycle.png");
}
#music-player header .btn-box .single-cycle {
        background-image: url("../assets/player_all_cycle.png");
}
#music-player header .btn-box .source {
        padding-left: 0.0463rem;
        color: #c8c8c8;
        font-size: 11px;
        background: url("../assets/xiami_logo.png") no-repeat;
        background-size: 0.03704rem 0.03704rem;
        background-position: 0 48%;
}
#music-player header .btn-box .collect {
        right: 0.10185rem;
        background-image: url("../assets/music_collection.png");
}
#music-player header .btn-box .detail-content {
        right: -0.01389rem;
        background-image: url("../assets/detail_content.png");
}
#music-player .mask {
    height: 100%;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.33);
}



