
.movie-header {
  position: relative;
}
.movie-header .movie-swiper {
    padding-top: 46px;
}
.movie-header .movie-swiper .vux-swiper-item {
      overflow: hidden;
      background-color: black;
}
.movie-header .movie-swiper img {
      width: 100%;
      height: auto;
}
.movie-header .photo-indicator {
    position: absolute;
    right: 0.02037rem;
    top: 226px;
    color: white;
    font-size: 12px;
}
.movie-header .moive-header-title .essay-title {
    background-size: 0.21296rem 0.00972rem;
}
.movie-header .moive-header-title h1 {
    padding-top: 0.00463rem;
    font-size: 17.5px;
    font-weight: 400;
    line-height: 1.4;
}



