
#author-cell {
  margin: 0 0.05833rem 0.06481rem 0.06019rem;
  padding-left: 0.00463rem;
}
#author-cell .author-label {
    height: 0.06944rem;
    width: 100%;
    margin-bottom: 0.06019rem;
    padding: 0 0 0.03704rem 0;
    font-size: 12.8px;
    background: -webkit-linear-gradient(black, black) no-repeat;
    background: linear-gradient(black, black) no-repeat;
    background-size: 0.19444rem 0.00833rem;
    background-position: 0% 100%;
}
#author-cell .author-info {
    position: relative;
    height: 0.11111rem;
}
#author-cell .author-info div {
      display: inline-block;
}
#author-cell .author-info .author-avtor {
      height: 0.11111rem;
      width: 0.11111rem;
      border-radius: 50%;
      overflow: hidden;
}
#author-cell .author-info .author-avtor img {
        padding: 0;
        width: 0.11111rem;
        height: 0.11111rem;
}
#author-cell .author-info .author-introduce {
      margin-left: 0.03241rem;
      top: -0.01389rem;
      height: 0.11111rem;
}
#author-cell .author-info .author-introduce p {
        width: 0.69444rem;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
}
#author-cell .author-info .author-introduce .author-name {
        font-size: 12.4px;
        line-height: 1.2;
        font-weight: 500;
}
#author-cell .author-info .author-introduce .author-name span {
          display: inline-block;
          font-size: 11px;
          -webkit-transform-origin-x: 0;
          -webkit-transform: scale(0.82);
          font-weight: 600;
}
#author-cell .author-info .author-introduce .author-desc {
        color: rgba(0, 0, 0, 0.2);
        font-size: 12px;
        -webkit-transform-origin-x: 0;
        -webkit-transform: scale(0.76);
        font-weight: 600;
}
#author-cell .author-info .btn-follow {
      position: absolute;
      top: 0.00926rem;
      right: 0.00278rem;
      padding: 0.01111rem 0.04167rem;
      text-align: center;
      line-height: 0.05556rem;
      font-size: 12px;
      -webkit-transform: scale(0.74);
      -webkit-transform-origin-x: 0;
      font-weight: 700;
      border-radius: 0.00926rem;
      border: 0.00185rem solid #8a8a8a;
}
#author-cell .author-info .followed {
      padding: 0.01111rem 0.02315rem;
      color: white;
      background: rgba(0, 0, 0, 0.65);
      font-weight: 600;
}



