
.listitem {
  height: 50px;
  line-height: 50px;
  border-bottom: solid 1px #eee;
  text-align: center;
}
.listitem:first-child {
    border-top: solid 1px #eee;
}
.loadmore {
  text-align: center;
}
.desc {
  text-align: center;
  color: #666;
  padding-bottom: 5px;
}
.desc:last-of-type {
    border-bottom: solid 1px #eee;
}
.mint-spinner {
  display: inline-block;
  vertical-align: middle;
}
span {
  display: inline-block;
  -webkit-transition: .2s linear;
  transition: .2s linear;
  vertical-align: middle;
}
.is-rotate {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}
.wrapper {
  overflow: scroll;
}
.mint-loadmore {
  overflow: hidden;
}



