
@import url(../styles/animation.css);
img[src=""] {
  opacity: 0;
}
.music-header {
  position: relative;
  padding-top: 1rem;
}
.music-header .music-header-box {
    position: absolute;
    padding: 0.04444rem;
    margin-bottom: 0.10185rem;
    height: 1.21481rem;
    width: 1.21481rem;
    top: -0.33333rem;
    left: -0.44074rem;
    border-top-right-radius: 50%;
    border-bottom-right-radius: 50%;
    box-shadow: 0.00741rem 0.00556rem 0.01389rem #f6f6f6;
}
.music-header .music-header-box .music-header-img {
      height: 1.12593rem;
      width: 1.12593rem;
      border-radius: 50%;
      -webkit-filter: brightness(0.9);
              filter: brightness(0.9);
      -webkit-animation: rotate 30s linear infinite;
              animation: rotate 30s linear infinite;
      -webkit-animation-play-state: paused;
              animation-play-state: paused;
}
.music-header .music-header-box .rotate {
      -webkit-animation-play-state: running;
              animation-play-state: running;
}
.music-header .music-header-box .muisc-header-xiami {
      position: absolute;
      height: 0.09352rem;
      width: 0.09352rem;
      top: 0.56852rem;
      right: 0.06019rem;
      border-radius: 0.00926rem;
}
.music-header .music-header-box .music-header-play-btn, .music-header .music-header-box .music-header-pause-btn {
      position: absolute;
      height: 0.17778rem;
      width: 0.17778rem;
      top: 0.52315rem;
      right: 0.51852rem;
      border-radius: 50%;
      background: rgba(250, 250, 250, 0.5);
}
.music-header .music-header-box .music-header-play-btn:after, .music-header .music-header-box .music-header-pause-btn:after {
        content: '';
        display: block;
        position: absolute;
}
.music-header .music-header-box .music-header-play-btn:after {
      top: 0.0463rem;
      left: 0.06019rem;
      border-top: 0.04259rem solid transparent;
      border-right: 0.07222rem solid transparent;
      border-bottom: 0.04259rem solid transparent;
      border-left: 0.07222rem solid white;
}
.music-header .music-header-box .music-header-pause-btn:after {
      height: 0.08519rem;
      width: 0.01667rem;
      top: 0.0463rem;
      left: 0.05556rem;
      border-right: 0.02778rem solid white;
      border-left: 0.02778rem solid white;
}
.music-header p {
    text-align: center;
    color: #c4c4c4;
}
.music-header .music-header-title {
    padding: 0 0.05556rem;
    padding-bottom: 0.00926rem;
    padding-top: 0.06944rem;
    font-weight: normal;
    font-size: 22px;
    line-height: 1.4;
}
.music-header .music-header-author {
    margin-top: 0.02315rem;
    margin-bottom: 0.02778rem;
    font-size: 13.2px;
    color: black;
}



