
.essay-header {
  padding: 0 0.05556rem;
}
.essay-header .essay-title {
    padding-bottom: 0.0463rem;
    padding-top: 0.18519rem;
    font-weight: normal;
    font-size: 22px;
    line-height: 1.4;
    background: -webkit-linear-gradient(black, black) no-repeat;
    background: linear-gradient(black, black) no-repeat;
    background-size: 0.21296rem 0.01389rem;
    background-position: 0% 100%;
}
.essay-header .author-label {
    display: inline-block;
    height: 0.05648rem;
    margin: 0.06481rem 0;
    line-height: 0.05648rem;
    font-size: 13.2px;
    font-weight: 500;
}



