
.comment-cell {
  padding-top: 0.04074rem;
  background-color: #fff;
}
.comment-cell .comment-header {
    height: 0.05185rem;
    margin-bottom: 0.02315rem;
    padding-left: 0.06019rem;
    padding-right: 0.04352rem;
    color: rgba(0, 0, 0, 0.3);
    font-weight: 700;
}
.comment-cell .comment-header div {
      display: inline-block;
}
.comment-cell .comment-header .comment-avtor {
      height: 0.05185rem;
      width: 0.05185rem;
      border-radius: 50%;
      overflow: hidden;
}
.comment-cell .comment-header .comment-avtor img {
        width: 0.05185rem;
        height: 0.05185rem;
}
.comment-cell .comment-header .comment-name {
      height: 0.05185rem;
      max-width: 0.41667rem;
      margin-left: 0.0213rem;
      font-size: 12px;
      line-height: 0.05185rem;
      font-weight: 500;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
}
.comment-cell .comment-header .comment-date {
      float: right;
      font-size: 12px;
      -webkit-transform-origin-x: 0;
      -webkit-transform: scale(0.8);
      font-weight: 500;
}
.comment-cell .comment-reply {
    margin: 0 0.06111rem 0.01852rem 0.13889rem;
    padding: 0.03241rem 0.06481rem 0.02315rem 0.02778rem;
    border: 0.00185rem solid rgba(0, 0, 0, 0.3);
}
.comment-cell .comment-reply .comment-reply-name {
      height: 0.03704rem;
      max-width: 0.34259rem;
      line-height: 0.03704rem;
      margin-bottom: 0.01111rem;
      font-weight: 600;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
}
.comment-cell .comment-reply .comment-reply-content {
      max-height: 0.11111rem;
      line-height: 0.05556rem;
      font-size: 12.5px;
      color: #7d7d7d;
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 2;
      overflow: hidden;
}
.comment-cell .comment-content {
    margin-left: 0.13889rem;
    margin-right: 0.08333rem;
    margin-bottom: 0.025rem;
    font-size: 12.8px;
    line-height: 0.06019rem;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 5;
    overflow: hidden;
}
.comment-cell .comment-footer .commnet-footer-content {
    height: 0.05556rem;
    margin-right: 0.09259rem;
    padding-bottom: rm(10);
}
.comment-cell .comment-footer .commnet-footer-content div {
      float: right;
      height: 0.05556rem;
      -webkit-transform-origin-x: 0;
      -webkit-transform: scale(0.8);
      font-size: 12px;
      color: rgba(0, 0, 0, 0.4);
      text-align: left;
}
.comment-cell .comment-footer .commnet-footer-content .comment-praise {
      width: 0.12963rem;
      padding-left: 0.08333rem;
      background: url("../assets/comment_laud.png") no-repeat;
      background-size: 0.0463rem 0.0463rem;
      background-position: 0.02593rem -0.00463rem;
}
.comment-cell .comment-footer .commnet-footer-content .praised {
      background-image: url("../assets/comment_laud_selected.png");
}
.comment-cell .comment-footer .commnet-footer-content .comment-btn {
      width: 0.11111rem;
      background: url("../assets/comment_image.png") no-repeat;
      background-size: 0.0463rem 0.0463rem;
      background-position: center 0.00093rem;
}
.comment-cell .comment-footer .comment-line {
    height: 0.02778rem;
    background-color: #fafafa;
}



