
#question-header {
  padding: 0 0.05556rem;
  padding-top: 46px;
}
#question-header .question-title {
    padding-top: 0.05556rem;
    padding-bottom: 0.03704rem;
    font-size: 22px;
    font-weight: 500;
}
#question-header .question-content, #question-header .question-asker {
    font-size: 12.7px;
    line-height: 1.98;
    font-weight: 600;
}
#question-header .question-asker {
    position: relative;
    padding-top: 0.02315rem;
    padding-bottom: 0.06481rem;
    border-bottom: 0.00556rem solid #f0f0f0;
}
#question-header .question-asker:after {
      content: '';
      position: absolute;
      bottom: -0.02593rem;
      left: 50%;
      margin-left: -0.02037rem;
      height: 0.01667rem;
      width: 0.01667rem;
      background-color: #f0f0f0;
      border: 0.01481rem solid white;
      border-radius: 50%;
}
#question-header .question-answerer {
    margin-top: 0.08796rem;
    margin-bottom: 0.07407rem;
    font-size: 18px;
    font-weight: 600;
}



