
html, body {
  height: 100%;
}
#home-container {
  height: 100%;
}
#home-container .header img {
    height: 46px;
    width: 46px;
}
#home-container main {
    padding-top: 46px;
    padding-bottom: 50px;
}
#home-container main .loadmore-wrapper {
      overflow: scroll;
}
#home-container main .loadmore {
      overflow: hidden;
}
.weui-tabbar__item.weui-bar__item_on .weui-tabbar__label span {
  color: rgba(51, 0, 0, 0.7);
}
.vux-x-icon {
  fill: #999999;
}
.weui-bar__item_on .vux-x-icon {
  fill: rgba(0, 0, 0, 0.7);
}
div.weui-tabbar {
  position: fixed;
}
.rotate {
  transform: rotate(180deg);
  -webkit-transform: rotate(180deg);
}
.pullup-arrow {
  display: block;
  transition: all linear 0.2s;
  -webkit-transition: all linear 0.2s;
  color: #666;
  font-size: 25px;
}
.scroller-container {
  height: 100%;
}
#vux-scroller-2811y {
  height: calc(100%-96px);
}



